.amb-sidenav {
  display: flex;
  flex-flow: column;
  text-align: center;
  min-height: 720px;
  width: 20%;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #fff;
  overflow-x: hidden;
  box-shadow: 7px 0px 10px 4px #eee;
}

.amb-sidenav a {
  margin: 40px auto;
  text-decoration: none;
  display: block;
  color: #707c97;
}

.amb-sidenav a:hover {
  margin: 40px auto;
  text-decoration: none;
  display: block;
  color: #000;
}

.nav-links {
  justify-content: center;
  align-items: center;
  flex: 80%;
}

.name {
  flex: 10%;
  margin: 75px auto 50px auto;
}

.logout {
  flex: 10%;
  margin: 0px auto 20px auto;
}

.amb-sidenav .active {
  color: #000;
  margin: 40px auto;
  text-decoration: none;
  display: block;
}

@media only screen and (max-width: 800px) {
  .amb-sidenav {
    display: none;
  }
}
